import { ref, nextTick, provide } from 'vue';
export default {
  setup() {
    const isRouterActive = ref(true);
    const reload = () => {
      isRouterActive.value = false;
      nextTick(function () {
        isRouterActive.value = true;
      });
    };
    provide('reload', reload);
    return {
      reload,
      isRouterActive
    };
  }
};